import React, { Component } from "react";
import { Table, message, Button, Modal, Checkbox, Pagination } from "antd";
import AddManager from "../tools/addManager.jsx"
import memoryUtils from "../../utils/memoryUtils.js";
import {
  reqClassManage,
  reqClassDelete,
  reqChooseExp,
  reqExpCheckbox
} from "../../api/index.js";
import store from "../../store";
import AddClassBtn from "./addClassBtn.jsx";
import BatchUsers from "./batchUsers.jsx";

const options = [
  { label: "AppleL", value: 1, disabled: false },
  { label: "PearL", value: 2, disabled: true },
  { label: "OrangeL", value: 3, disabled: true }
];

export default class ClassManageCom extends Component {
  constructor(props) {
    super(props);

    this.state = {
      //
      pageSize: 10,
      //
      pageNum: 1,
      //
      //
      position: ["bottomCenter"],
      adminId: "1",
      records: "",
      classId: "",
      //
      isLoading: true,
      //modal
      visible: false,
      //modal
      deleteVisible: false,
      chooseVisible: false,
      className: "",
      // 
      isDelete: false,
      //
      checkedValues: [],
      //
      options: []
    };
  }

  /**
   * @description:
   * @param {adminId ID}
   * @return:
   */
  funcReqClassManage = adminId => {
    reqClassManage(adminId)
      .then(response => {
        if (response.isSuccess) {
          this.setState({
            records: response.data,
            isLoading: false
          });
        } else {
          message.error(response.message);
        }
      })
      .catch(error => {
        message.error(error.message);
      });
  };

  /**
   * @description:Id
   * @param {type}
   * @return:
   */
  componentWillMount() {
    this.setState(
      () => {
        return { adminId: memoryUtils.user.adminId };
      },
      () => {
        const { adminId } = this.state;
        this.funcReqClassManage(adminId);
      }
    );
  }

  /**
   * @description: store
   * @param {className , pageNum , pageSize , adminId ID, type }
   * @return:
   */
  dispatchFunc = (className, pageNum, pageSize, adminId, type) => {
    store.dispatch({
      type: type,
      payload: {
        className: className,
        pageNum: pageNum,
        pageSize: pageSize,
        adminId: adminId
      }
    });
  };

  /**
   * @description:,classNamepageNumpageSizeadminID store
   * @param {text , record , index }
   * @return:
   */
  handleSeePeople = (text, record, index) => {
    const { pageNum, pageSize, adminId } = this.state;
    this.dispatchFunc(
      text.className,
      pageNum,
      pageSize,
      adminId,
      "classNameChange"
    );
    this.props.history.push("/people");
  };

  /**
   * @description: 
   * @param {classId ID}
   * @return:
   */
  handleDeleteClass = classId => {
    reqClassDelete(this.state.adminId, classId)
      .then(response => {
        if (response.isSuccess) {
          this.setState(
            () => {
              return { isDelete: true };
            },
            () => {
              this.funcReqClassManage(this.state.adminId);
              this.setState({ isDelete: false });
            }
          );
          message.success("");
        } else {
          message.error("");
        }
      })
      .catch(error => {
        message.error(error.message);
      });
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   */
  showModal = () => {
    this.setState({
      visible: true
    });
  };

  showDeleteModal = text => {
    this.setState({
      deleteVisible: true,
      classId: text.classId,
      className: text.className
    });
  };

  /**
   * @description:  - 
   * @param {text }
   * @return: data[{label:'',value: }]options
   */
  showChooseExpModal = record => {
    reqExpCheckbox(record.classId)
      .then(response => {
        if (response.isSuccess) {
          var array = new Array();
          for (var i = 0; i < response.data.experimentDTOS.length; i++) {
            var obj = new Object();
            obj.label = response.data.experimentDTOS[i].experimentTitle;
            obj.value = response.data.experimentDTOS[i].experimentId;
            obj.disabled = false;
            for (var j = 0; j < response.data.isSelected.length; j++) {
              if (obj.value === response.data.isSelected[j]) {
                obj.disabled = true;
                break;
              } else continue;
            }
            array[i] = obj;
          }
          this.setState(
            () => {
              return { options: array };
            },
            () => {
              this.setState({
                chooseVisible: true,
                classId: record.classId,
                className: record.className
              });
            }
          );
        } else {
          message.error("");
        }
      })
      .catch(error => {});
  };
  /**
   * @description: 
   * @param {type}
   * @return:
   */
  handleOk = e => {
    this.setState({
      visible: false
    });
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   */
  handleCancel = e => {
    this.setState({
      visible: false
    });
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   */
  deleteHandleOk = e => {
    this.setState({
      deleteVisible: false
    });
    this.handleDeleteClass(this.state.classId);
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   */
  deleteHandleCancel = e => {
    this.setState({
      deleteVisible: false
    });
  };

  /**
   * @description: 
   * @param {e }
   * @return:
   */
  chooseExpHandleOk = e => {
    this.setState({
      chooseVisible: false
    });
    this.funcReqChooseExp(this.state.classId, this.state.checkedValues);
  };

  /**
   * @description: 
   * @param {e }
   * @return:
   */
  chooseExpHandleCancel = e => {
    this.setState({
      chooseVisible: false
    });
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   */
  rerender = ok => {
    this.funcReqClassManage(this.state.adminId);
  };

  /**
   * @description: 
   * @param {classId ID,checkedValues }
   * @return:
   */
  funcReqChooseExp = (classId, checkedValues) => {
    reqChooseExp(classId, checkedValues)
      .then(response => {
        if (response.isSuccess) {
          message.success("");
        } else {
          message.error("");
          this.setState({ checkedValues: [] });
        }
      })
      .catch(error => {
        message.error(error.message);
      });
  };

  onChange = checkedValues => {
    this.setState({ checkedValues });
  };

  render() {
    const columns = [
      {
        title: "",
        dataIndex: "key",
        key: "key",
        //
        sorter: (a, b) => a.key - b.key,
        //
        render: (text, record, index) => `${index + 1}`,
        //
        width: "5vw",
        fixed: "left"
      },
      {
        title: "",
        dataIndex: "className",
        key: "className",
        sorter: (a, b) => a.key - b.key,
        width: "7vw"
      },
      {
        title: "",
        dataIndex: "classDetail",
        key: "classDetail",
        width: "10vw"
      },
      {
        title: "",
        dataIndex: "classId",
        key: "classId",
        width: "5vw"
      },
      {
        title: "",
        key: "operation",
        fixed: "right",
        width: "15vw",
        render: (text, record, index) => {
          return (
            <div className="site-button-ghost-wrapper">
              <Button
                type="primary"
                onClick={() => {
                  this.setState(
                    () => {
                      return { classId: text.classId };
                    },
                    () => {
                      this.showModal();
                    }
                  );
                }}
              >
                
              </Button>
              <Modal
                title=""
                visible={this.state.visible}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
                okText=""
                cancelText=""
              >
                <BatchUsers classId={this.state.classId} />
              </Modal>{" "}
              <Button type="danger" onClick={() => this.showDeleteModal(text)}>
                
              </Button>
              <Modal
                title=""
                visible={this.state.deleteVisible}
                onOk={this.deleteHandleOk}
                onCancel={this.deleteHandleCancel}
                okText=""
                cancelText=""
              >
                <p>{this.state.className}</p>
              </Modal>{" "}
              <Button
                type="primary"
                onClick={() => this.handleSeePeople(text, record, index)}
              >
                
              </Button>{" "}
              <Button
                type="primary"
                onClick={() => this.showChooseExpModal(record)}
              >
                
              </Button>
              <Modal
                title=""
                visible={this.state.chooseVisible}
                onOk={this.chooseExpHandleOk}
                onCancel={this.chooseExpHandleCancel}
                okText=""
                cancelText=""
              >
                <Checkbox.Group
                  options={this.state.options}
                  defaultValue={this.state.checkedValues}
                  onChange={this.onChange}
                />
                {/* {this.state.options.map((items, index) => {
                  return (
                    <Checkbox key={items.experimentId} onChange={this.singleCheckedOnChange}>Checkbox</Checkbox>
                    
                  );
                })} */}
              </Modal>
            </div>
          );
        }
      }
    ];
    return (
      <div>
        <div style={{display:"inline"}}>
            <AddClassBtn rerender={this.rerender} />
            {/* <AddManager /> */}
        </div>
        <div>
          <Table
            rowKey={record => record.classId}
            loading={this.state.isLoading}
            columns={columns}
            bordered={true}
            dataSource={this.state.records}
            scroll={{ x: 1000 }}
            pagination={<Pagination showSizeChanger={false} />}
          />
        </div>
      </div>
    );
  }
}
