import React, { Component } from "react";
import { Table, Tooltip, message } from "antd";

import "./feedbackCom.css";
import { reqFeedbackReports } from "../../api/index.js";

export default class FeedbackCom extends Component {
  constructor(props) {
    super(props);

    this.state = {
      //
      pageSize: 8,
      //
      pageNum: 1,
      //
      records: [],
      //
      total: 1,
      //
      pages: 1,
      //
      isLoading: true,
      //
      position: ["bottomCenter"]
    };
  }

  /**
   * @description: 
   * @param {pageSize , pageNum }
   * @return:state
   */
  funReqPageCon = (pageSize, pageNum) => {
    reqFeedbackReports(pageSize, pageNum)
      .then(response => {
        if (response.isSuccess) {
          this.setState({
            records: response.data.records,
            total: response.data.total,
            pages: response.data.pages,
            pageNum: response.data.current,
            pageSize: response.data.size,
            isLoading: false
          });
        } else {
          message.error(response.message);
        }
      })
      .catch(error => {
        message.error(error.message);
      });
  };

  /**
   * @description: 
   * @param {}
   * @return:
   */
  componentWillMount() {
    let { pageSize, pageNum } = this.state;
    this.funReqPageCon(pageSize, pageNum);
  }

  /**
   * @description: 
   * @param {pageNum }
   * @return: 
   */
  handleTableChange = pageNum => {
    let { pageSize } = this.state;
    this.setState(
      () => {
        return { pageNum: pageNum };
      },
      () => {
        this.funReqPageCon(pageSize, pageNum);
      }
    );
  };

  render() {
    const { records, pageNum, total, pageSize, position } = this.state;
    const columns = [
      {
        title: "",
        dataIndex: "key",
        key: "key",
        //
        sorter: (a, b) => a.key - b.key,
        //
        render: (text, record, index) =>
          `${pageSize * (pageNum - 1) + index + 1}`,
        //
        width: "5vw"
      },
      {
        title: "",
        dataIndex: "userStudentNumber",
        key: "userStudentNumber",
        sorter: (a, b) => a.key - b.key,
        width: "10vw"
      },
      {
        title: "",
        dataIndex: "userName",
        key: "userName",
        width: "7vw"
      },
      {
        title: "",
        dataIndex: "reportedTitle",
        key: "reportedTitle",
        ellipsis: true,
        width: "10vw",
        //
        ellipsis: {
          showTitle: false
        },
        //tooltip
        render: reportedTitle => (
          <Tooltip placement="topLeft" title={reportedTitle}>
            {reportedTitle}
          </Tooltip>
        )
      },
      {
        title: "",
        dataIndex: "reportedDetail",
        key: "reportedDetail",
        ellipsis: true,
        width: "15vw",
        ellipsis: {
          showTitle: false
        },
        render: reportedDetail => (
          <Tooltip placement="topLeft" title={reportedDetail}>
            {reportedDetail}
          </Tooltip>
        )
      },
      {
        title: "",
        dataIndex: "createTime",
        key: "createTime",
        width: "10vw",
        //
        sorter: (a, b) => Date.parse(a.createTime) - Date.parse(b.createTime)
      }
    ];

    //
    const paginationProps = {
      //
      page: pageNum,
      //
      defaultPageSize: pageSize,
      //
      onChange: page => this.handleTableChange(page),
      //
      total: total,
      //
      position: position
    };
    return (
      <div>
        <Table
          columns={columns}
          bordered={true}
          dataSource={records}
          pagination={paginationProps}
          loading={this.state.isLoading}
        />
      </div>
    );
  }
}
