import React from "react";
import "./cAExperiment.css";
import { Table, Button, Switch, message, Radio, Popconfirm,Layout } from "antd";
import { DeleteTwoTone } from "@ant-design/icons";
import memoryUtils from "../../../utils/memoryUtils.js";
import {
  reqExpShow,
  reqClassRadio,
  reqDeleteExp,
  reqExpStatus
} from "../../../api/index.js";
const { Content,Footer } = Layout;      //
export default class ConAExperiment extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      children: "",
      //
      isLoading: true,
      records: [],
      classId: "",
      isChecked: [],
      experimentId: "",
      radioData: []
    };
  }

  /**
   * @description: 
   * @param {classId Id}
   * @return:
   */
  funcReqExp = classId => {
    const { adminId } = this.state;

    reqExpShow(adminId, classId)
      .then(response => {
        if (response.isSuccess) {
          this.setState({
            records: response.data,
            isLoading: false
          });
        } else {
          message.error(response.message);
        }
      })
      .catch(error => {
        message.error(error.message);
      });
  };

  /**
   * @description: 
   * @param {adminId ID}
   * @return:
   */
  funcReqClassRadio = adminId => {
    this.setState({ isLoading: true });
    reqClassRadio(adminId)
      .then(response => {
        if (response.isSuccess) {
          this.setState({
            radioData: response.data,
            isLoading: false
          });
        } else {
          message.error(response.message);
        }
      })
      .catch(error => {
        message.error(error.message);
      });
  };

  /**
   * @description: 
   * @param {classId ,expId }
   * @return:
   */
  funcReqDeleteExp = (classId, expId) => {
    reqDeleteExp(classId, expId)
      .then(response => {
        if (response.isSuccess) {
          this.setState(
            () => {
              return { isLoading: false };
            },
            () => {
              this.funcReqExp(this.state.classId);
            }
          );
          message.success("");
        } else {
          message.error(response.message);
        }
      })
      .catch(error => {
        message.error(error.message);
      });
  };

  /**
   * @description:
   * @param {expId ID, classId ID, status 0 1}
   * @return:
   */
  funcExpStatus = (expId, classId, status) => {
    reqExpStatus(expId, classId, status)
      .then(response => {
        if (response.isSuccess) {
          this.setState(
            () => {
              return { isLoading: false };
            },
            () => {
              this.funcReqExp(this.state.classId);
            }
          );
        } else {
          message.error(response.message);
        }
      })
      .catch(error => {
        message.error(error.message);
      });
  };

  componentWillMount() {
    this.setState({ adminId: memoryUtils.user.adminId });
    this.setState(
      () => {
        return { adminId: memoryUtils.user.adminId };
      },
      () => {
        //
        this.funcReqExp();
        //
        this.funcReqClassRadio(this.state.adminId);
      }
    );
  }

  /**
   * @description:  
   * @param {e }
   * @return:  e.target.value Id
   */
  handleRadio = e => {
    let classId = e.target.value;
    this.setState({ classId });
    if (classId === "") this.funcReqExp();
    else this.funcReqExp(classId);
  };

  /**
   * @description: 
   * @param {checked (truefalse), expId ID, classId ID}
   * @return:
   */
  openSwitch = (checked, expId, classId) => {
    let status = checked ? 0 : 1;
    this.funcExpStatus(expId, classId, status);
  };

  /**
   * @description:  
   * @param {record }
   * @return:
   */
  deleteConfirm = record => {
    this.handleDeleteExp(record);
  };

  deleteCancel = e => {};
  /**
   * @description: 
   * @param {record }
   * @return:
   */
  handleDeleteExp = record => {
    let expId = record.experimentId;
    let classId = record.classId;
    this.funcReqDeleteExp(classId, expId);
  };

  render() {
    const columns = [
      // {
      //   title: "",
      //   dataIndex: "key",
      //   key: "key",
      //   //
      //   sorter: (a, b) => a.key - b.key,
      //   //
      //   render: (text, record, index) => `${index + 1}`,
      //   //
      //   width: "5vw"
      // },
      {
        title: "",
        dataIndex: "classId",
        key: "classId",
        width: "5vw"
      },
      {
        title: "",
        dataIndex: "className",
        key: "className",
        width: "7vw"
      },
      {
        title: "",
        dataIndex: "experimentTitle",
        key: "experimentTitle",
        width: "7vw"
      },
      {
        title: "",
        dataIndex: "experimentDescribe",
        key: "experimentDescribe",
        width: "14vw"
      },
      {
        title: "",
        dataIndex: "operation",
        key: "operation",
        fixed: "right",
        width: "10vw",
        render: (text, record, index) => {
          return (
            <div>
              <Switch
                checkedChildren=""
                unCheckedChildren=""
                checked={record.isClosed ? false : true}
                onClick={checked =>
                  this.openSwitch(checked, record.experimentId, record.classId)
                }
                key={record.experimentId}
              />{" "}
              <Popconfirm
                title=""
                onConfirm={() => this.deleteConfirm(record)}
                onCancel={() => this.deleteCancel(record)}
                okText=""
                cancelText=""
              >
                <Button
                  shape="round"
                  icon={<DeleteTwoTone />}
                  // onClick={() => this.handleDeleteExp(record)}
                />
              </Popconfirm>
            </div>
          );
        }
      }
    ];

    return (
      <Layout style={{ padding: '0 24px 24px', margin: '23px 0' }}>
        <Content className="site-layout-background BaseContent" >
          <div>
            <Radio.Group
              className="radioGroup"
              buttonStyle="solid"
              onChange={this.handleRadio}
              defaultValue=""
            >
              {this.state.radioData.map((items, index) => {
                return (
                  <Radio.Button value={items.classId} key={index}>
                    {items.className}
                  </Radio.Button>
                );
              })}
              <Radio.Button value={""} key={99}>
                
              </Radio.Button>
            </Radio.Group>

            <Table
              style={{ padding: "24px 24px" }}
              loading={this.state.isLoading}
              columns={columns}
              bordered={true}
              rowKey={record =>
                new Date().getTime().toString(36) +
                record.experimentId.toString() +
                record.classId.toString()
              }
              dataSource={this.state.records}
              scroll={{ y: "70vh" }}
              pagination={false}
            />
          </div>
      </Content>
      {/* <Footer style={{ textAlign: 'center',height:"5px" }}>2020 </Footer> */}
      </Layout>
    );
  }
}
