import React, { Component } from "react";
import { Table, Input, message, Popconfirm, Form } from "antd";
import { reqTableData, reqTableChangeData } from "../../api/index.js";

interface Item {
  key: string;
  experimentId: number;
  experimentTitle: string;
  experimentDescribe: string;
  experimentTask: string;
}

interface EditableCellProps extends React.HTMLAttributes<HTMLElement> {
  //
  editing: boolean;
  //
  dataIndex: string;
  //
  title: any;
  //
  inputType: "number" | "text";
  record: Item;
  index: number;
  children: React.ReactNode;
}

//React.FC<>typescriptuseState
const EditableCell: React.FC<EditableCellProps> = ({
  record,
  editing,
  dataIndex,
  title,
  inputType,
  col,
  children,
  ...restProps
}) => {
  return (
    <td {...restProps}>
      {/* ?form: */}
      {editing ? (
        <Form.Item
          name={dataIndex}
          style={{ margin: 0 }}
          rules={[
            {
              required: true,
              message: `!`
            }
          ]}
        >
          <Input defaultValue={children[1]} />
        </Form.Item>
      ) : (
        children
      )}
    </td>
  );
};

export default class DTable extends Component {
  constructor(props) {
    super(props);

    this.state = {
      //
      editingKey: "",
      //
      data: [
        {
          experimentId: 1,
          experimentTitle: "",
          experimentDescribe: "1 Nmap",
          experimentTask: "",
          imageId: "",
          imageDescribe: "",
          imageName: ""
        },
        {
          experimentId: 2,
          experimentTitle: "test",
          experimentDescribe:
            "1 NmapNmap",
          experimentTask: "",
          imageId: "",
          imageDescribe: "",
          imageName: ""
        },
        {
          experimentId: 3,
          experimentTitle: "@",
          experimentDescribe: "1 Nmap",
          experimentTask: "",
          imageId: "",
          imageDescribe: "",
          imageName: ""
        },
        {
          experimentId: 4,
          experimentTitle: "@",
          experimentDescribe: "1 Nmap",
          experimentTask: "",
          imageId: "",
          imageDescribe: "",
          imageName: ""
        },
        {
          experimentId: 5,
          experimentTitle: "@",
          experimentDescribe: "1 Nmap",
          experimentTask: "",
          imageId: "",
          imageDescribe: "",
          imageName: ""
        },
        {
          experimentId: 6,
          experimentTitle: "web",
          experimentDescribe: "web",
          experimentTask: "1.sql 2.xss 3.crsf",
          imageId: "bf756fb1ae65",
          imageDescribe: "",
          imageName: ""
        },
        {
          experimentId: 7,
          experimentTitle: "web",
          experimentDescribe: "web",
          experimentTask: "1.sql 2.xss 3.crsf",
          imageId: "bf756fb1ae65",
          imageDescribe: "",
          imageName: ""
        },
        {
          experimentId: 8,
          experimentTitle: "web",
          experimentDescribe: "web",
          experimentTask: "1.sql 2.xss 3.crsf",
          imageId: "bf756fb1ae65",
          imageDescribe: "",
          imageName: ""
        },
        {
          experimentId: 9,
          experimentTitle: "web",
          experimentDescribe: "web",
          experimentTask: "1.sql 2.xss 3.crsf",
          imageId: "bf756fb1ae65",
          imageDescribe: "",
          imageName: ""
        },
        {
          experimentId: 10,
          experimentTitle: "web",
          experimentDescribe: "web",
          experimentTask: "1.sql 2.xss 3.crsf",
          imageId: "bf756fb1ae65",
          imageDescribe: "",
          imageName: "DMVY"
        }
      ],
      //
      changeAllValues: {
        experimentTask: "",
        experimentTitle: "",
        experimentDescribe: ""
      }
    };
  }

  /**
   * @description: 
   * @param {type}
   * @return {type}
   */
  componentWillMount() {
    this.funcReqTableData();
  }

  /**
   * @description: 
   * @param {type}
   * @return {type}
   */
  funcReqTableData = () => {
    reqTableData()
      .then(response => {
        if (response.isSuccess) this.setState({ data: response.data });
        else message.error("");
      })
      .catch(error => {
        message.error(error.message);
      });
  };

  /**
   * @description: experimentId
   * @param {record }
   * @return {type}
   */
  isEditing = (record: Item) => record.experimentId === this.state.editingKey;

  /**
   * @description: editingKey
   * @param {type}
   * @return {type}
   */
  cancel = () => {
    this.setState({ editingKey: "" });
  };

  /**
   * @description: 
  //
   * @param {changedValues , allValues {experimentTask: "60", experimentTitle: "John Brownsss"}} 
   * @return {type} 
   */
  handleValueChange = (changedValues, allValues) => {
    this.setState({ changeAllValues: allValues });
  };

  /**
   * @description: 
   * @param {key : {experimentId: "1", experimentTitle: "John Brown", experimentDescribe: 98, experimentTask: 60} }
   * @return {type}
   */
  save = (key: React.Key) => {
    //
    const { changeAllValues } = this.state;
    try {
      //newData 
      const newData = [...this.state.data];
      //  index 
      const index = newData.findIndex(
        item => key.experimentId === item.experimentId
      );
      //
      const item = newData[index];
      //
      if (changeAllValues.experimentTask)
        item.experimentTask = changeAllValues.experimentTask;
      if (changeAllValues.experimentTitle)
        item.experimentTitle = changeAllValues.experimentTitle;
      if (changeAllValues.experimentDescribe)
        item.experimentDescribe = changeAllValues.experimentDescribe;
      //
      newData.splice(index, 1, {
        ...item
      });
      //dataindex
      this.setState(
        () => {
          return { data: newData, editingKey: "" };
        },
        () => {
          this.funcReqTableChangeData(item);
        }
      );
    } catch (errInfo) {
    }
  };

  /**
   * @description: 
   * @param {type}
   * @return {type}
   */
  funcReqTableChangeData = item => {
    const {
      experimentDescribe,
      imageId,
      imageName,
      imageDescribe,
      experimentTitle,
      experimentId,
      experimentTask
    } = item;
    reqTableChangeData(
      experimentDescribe,
      imageId,
      imageName,
      imageDescribe,
      experimentTitle,
      experimentId,
      experimentTask
    )
      .then(response => {
        if (response.isSuccess) {
          message.success("");
          this.funcReqTableData();
        } else message.error("");
      })
      .catch(error => {
        message.error(error.message);
      });
  };

  /**
   * @description: experimentId
   * @param {type}
   * @return {type}
   */
  edit = (record: Item) => {
    this.setState({ editingKey: record.experimentId });
  };

  render() {
    /**
     * @description: 
     * @param {type}
     * @return {type}
     */
    const columns = [
      {
        title: "ID",
        dataIndex: "experimentId",
        width: "6vw"
      },
      {
        title: "",
        dataIndex: "experimentTitle",
        width: "20vw",
        editable: true
      },
      {
        title: "",
        dataIndex: "experimentDescribe",
        width: "25vw",
        editable: true
      },
      {
        title: "",
        dataIndex: "experimentTask",
        width: "25vw",
        editable: true
      },
      {
        title: "ID",
        dataIndex: "imageId",
        width: "6vw"
      },
      {
        title: "",
        dataIndex: "imageName",
        width: "10vw"
      },
      {
        title: "",
        dataIndex: "imageDescribe",
        width: "20vw"
      },
      {
        title: "operation",
        dataIndex: "operation",
        fixed: "right",
        width: "10vw",
        // render: (_: any, record: Item) => {
        render: (text, record: Item, index) => {
          //experimentId
          const editable = this.isEditing(record);
          return editable ? (
            <span>
              <a
                href
                onClick={() => this.save(record)}
                style={{ marginRight: 8 }}
              >
                
              </a>
              <Popconfirm title="" onConfirm={this.cancel}>
                <a href></a>
              </Popconfirm>
            </span>
          ) : (
            <a
              href
              disabled={this.state.editingKey !== ""}
              onClick={() => this.edit(record)}
            >
              
            </a>
          );
        }
      }
    ];

    /**
     * @description: 
     * @param {type}
     * @return {type}
     */
    const mergedColumns = columns.map(col => {
      if (!col.editable) {
        return col;
      }
      return {
        ...col,
        onCell: (record: Item) => ({
          record,
          col,
          inputType: col.dataIndex === "experimentId" ? "number" : "text",
          dataIndex: col.dataIndex,
          //change
          title: record.key,
          editing: this.isEditing(record)
        })
      };
    });

    return (
      <div>
        <Form component={false} onValuesChange={this.handleValueChange}>
          <Table
            //
            components={{
              body: {
                cell: EditableCell
              }
            }}
            bordered
            dataSource={this.state.data}
            columns={mergedColumns}
            rowClassName="editable-row"
            scroll={{ x: 1300 }}
            pagination={false}
          />
        </Form>
      </div>
    );
  }
}
