import React, { Component } from "react";
import { Drawer, Form, Button, Input, Col, message, Modal,Radio } from "antd";

import { reqAddClass, reqAddManager } from "../../api/index.js";
import "./classManageCom.css";
import memoryUtils from "../../utils/memoryUtils.js";
import { PlusOutlined } from "@ant-design/icons";

const layout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 20 },
};

export default class AddClassBtn extends Component {
  constructor(props) {
    super(props);

    this.state = {
      //
      visible: false,
      //
      checkVisible: false,
      //
      isAdd: false,
/////////////////////////////////
      //
      isOpen:false,
      //
      isSubOpen:false,
      //
      isCheck: false,
      //
      isAddManager: false,
/////////////////////////////////
      adminId: 0,
      className: "",
      classDetail: "",
      isSuper:"",
      adminAccount:"",
      adminTel:"",
      adminName:"",
      adminPassword:"",
      isOk: false,
      ModalText: '',
      confirmLoading: false,
      value:0
    };
  }

  componentWillMount() {
    this.setState({ 
      adminId: memoryUtils.user.adminId
    });
  }

  /**
   * @description: ,visible
   * @param {type}
   * @return:
   */
  showDrawer = () => {
    this.setState({
      visible: true
    });
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   */
  onClose = () => {
    this.setState({
      visible: false
    });
  };

  /**
   * @description: state
   * @param {type}
   * @return:
   */
  onFinish = values => {
    const { className, classDetail } = values;
    this.setState({
      checkVisible: true,
      className: className,
      classDetail: classDetail
    });
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   */
  handleOk = e => {
    const { adminId, className, classDetail } = this.state;
    this.setState(
      () => {
        return { checkVisible: false, isAdd: true };
      },
      () => {
        if (this.state.isAdd) {
          reqAddClass(className, classDetail, adminId)
            .then(response => {
              if (response.isSuccess === 1) {
                message.success("");
                this.setState({ isOk: true });
              } else {
                message.error("");
              }
            })
            .catch(error => {
            });
        }
      }
    );
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   */
  handleCancel = e => {
    this.setState({
      checkVisible: false,
      isAdd: false
    });
  };
  funcRender = (isOk) =>{
    this.props.rerender(isOk)
    this.setState({isOk:false})
  }

  
  /**
   * @description: 
   * @param {type}
   * @return:
   */
  showModal = () => {
    this.setState({
      isOpen: true,
    });
  };

 /**
   * @description: 
   * @param {type}
   * @return:
   */
  showSubModal = () => {
    this.setState({
      isSubOpen: true,
    });
  };

 /**
   * @description: state
   * @param {type}
   * @return:
   */
  onAddFinish = values => {
    const { isSuper,adminAccount,adminTel,adminName,adminPassword } = values;
    this.setState({
      isSubOpen:true,
      isCheck: true,
      isSuper:this.state.value,
      adminAccount:adminAccount,
      adminTel:adminTel,
      adminName:adminName,
      adminPassword:adminPassword
    });
  };
  

  /**
   * @description: 
   * @param {type}
   * @return:
   */
  handleAddOk = e => {
    const {isSuper,adminAccount,adminTel,adminName,adminPassword } = this.state;
    this.setState(
      () => {
        return { isCheck: false, isAddManager: true };
      },
      () => {
        if (this.state.isAddManager) {
          reqAddManager(isSuper,adminAccount,adminTel,adminName,adminPassword)
            .then(response => {
              if (response.isSuccess === 1) {
                message.success("");
                this.setState({
                      confirmLoading: true,
                    });
                    setTimeout(() => {
                      this.setState({
                        isOpen: false,
                        isSubOpen:false,
                        confirmLoading: false,
                      });
                    }, 2000);
              } else {
                message.error("");
              }
            })
            .catch(error => {
            });
        }
      }
    );
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   */
  handleAddCancel = () => {
    this.setState({
      isCheck: false,
      isOpen: false
    });
  };

  /**
   * @description: Sub
   * @param {type}
   * @return:
   */
  handleSubCancel = e => {
    this.setState({
      isSubOpen: false
    });
  };

  onChange = e => {
    this.setState({
      value: e.target.value,
    });
  };
  
  render() {
    const { isOpen, confirmLoading, ModalText } = this.state;
    return (
      <div
        rerender={this.state.isOk ? 
        this.funcRender(this.state.isOk)
        : 
        ""
        }
      >
        <Button
          type="primary"
          onClick={this.showDrawer}
          className="addClassBtn"
        >
          {" "}
          <PlusOutlined />
          
        </Button>
        <Drawer
          title=""
          width={700}
          onClose={this.onClose}
          visible={this.state.visible}
          bodyStyle={{ paddingBottom: 80 }}
          footer={
            <div
              style={{
                textAlign: "right"
              }}
            >
              <Button onClick={this.onClose} style={{ marginRight: 8 }}>
                
              </Button>
            </div>
          }
        >
          <Form layout="vertical" hideRequiredMark onFinish={this.onFinish}>
            <Form.Item
              name="className"
              label=""
              rules={[{ required: true, message: "" }]}
              hasFeedback
            >
              <Input placeholder="" />
            </Form.Item>

            <Form.Item
              name="classDetail"
              label=""
              rules={[
                {
                  required: true,
                  message: ""
                }
              ]}
              hasFeedback
            >
              <Input.TextArea rows={4} placeholder="" />
            </Form.Item>
            <Form.Item>
              <Col span={8} offset={11}>
                <Button type="primary" htmlType="submit">
                  
                </Button>
              </Col>
            </Form.Item>
            <Modal
              title=""
              visible={this.state.checkVisible}
              onOk={this.handleOk}
              onCancel={this.handleCancel}
              okText=""
              cancelText=""
            >
              <p></p>
            </Modal>
          </Form>
        </Drawer>

      <Button 
        type="primary" 
        onClick={this.showModal}
        className="addManager" 
      >{" "}
      
        <PlusOutlined />
        
      </Button>
      <Modal
        title=""
        onCancel={this.handleAddCancel}
        visible={this.state.isOpen}
        confirmLoading={confirmLoading}
        footer={[
          <Button key="cancle" onClick={this.handleAddCancel}>
            
          </Button>
        ]}
      >
      <Form {...layout} name="nest-messages" onFinish={this.onAddFinish} >
      <Form.Item
        label="" 
        // rules={[{ required: true }]}
        name="isSuper"
      >
        <Radio.Group 
          defaultValue={0} 
          onChange={this.onChange} 
          value={this.state.value}
        >
          <Radio value={1}></Radio>
          <Radio value={0}></Radio>
        </Radio.Group>
      </Form.Item>
      <Form.Item 
        name="adminName" 
        label="" 
        rules={[{ required: true,message:'!'}]}
        hasFeedback
      >
        <Input placeholder=''/>
      </Form.Item>
      <Form.Item 
        name="adminAccount" 
        label="" 
        rules={[{required: true,message:'!'}]} 
      >
        <Input placeholder='' />
      </Form.Item>
      <Form.Item 
        name="adminTel" 
        label="" 
        rules={[{min:11,max:11,required: true,message:""}]} 
        hasFeedback
      >
        <Input placeholder='11' />
      </Form.Item>
      <Form.Item 
        name="adminPassword" 
        label="" 
        rules={[{required: true, message: '!', min: 6, max: 30}]}
        hasFeedback
      >
        <Input.Password placeholder= '6'/>
      </Form.Item>
      <Form.Item 
        name="confirm" 
        label="" 
        dependencies={['password']} 
        rules={[{ required: true, message: '!'},
          ({ getFieldValue }) => ({
            validator(rule, value) {
              if (!value || getFieldValue('adminPassword') === value) {
                return Promise.resolve();
              }
              return Promise.reject('!');
            },
          }),
        ]}
        hasFeedback
      >
        <Input.Password />
      </Form.Item>
      <Form.Item>
        <Col span={10} offset={12}>
          <Button type="primary" htmlType="submit" >
            
          </Button>
        </Col>
      </Form.Item>
      <Modal
        title=""
        visible={this.state.isSubOpen}
        onOk={this.handleAddOk}
        onCancel={this.handleSubCancel}
        okText=""
        cancelText=""
      >
        <p></p>
      </Modal>
      </Form>
      </Modal>
    </div>
    );
  }
}
