import React, { Component } from "react";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import { Form, Input, message, Button, Row, Col, Tooltip } from "antd";

import {
  QuestionCircleOutlined,
} from '@ant-design/icons';
import "./SExpForm.less";
import { reqImage } from "../../api/index.js";

const { TextArea } = Input;

const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0
    },
    sm: {
      span: 16,
      offset: 8
    }
  }
};

export default class SExpForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      value: "",
      setValue: ""
    };
  }

  handleChange = value => {
    this.setState({ value });
  };

  handleSubmit = values => {
    const {
      expTitle,
      imageName,
      imagePos,
      imageDes,
      expSynopsis,
      expTask
    } = values;
    const { value } = this.state;
    reqImage(
      expTitle,
      expSynopsis,
      imagePos,
      expTask,
      value,
      imageName,
      imageDes
    )
      .then(response => {
        if (response.isSuccess === 1) {
          message.success("");
        }
      })
      .catch(error => {
        message.error(error.message);
      });
  };

  render() {
    const style = { margin: "0 5vw" };
    var showHtmlStyle = {
      background: "#f0f0f0",
      padding: "2px",
      height: "auto",
      minHeight: "20vh",
      overflow: "hidden"
    };
    return (
      <div style={style}>
        <Form onFinish={this.handleSubmit} name="register" scrollToFirstError>
          <Row>
            <Col span={11}>
              <Form.Item
                name="expTitle"
                label=""
                rules={[
                  {
                    required: true,
                    message: ""
                  }
                ]}
                hasFeedback
              >
                <Input />
              </Form.Item>
            </Col>
            <Col span={11} offset={2}>
            
               <Form.Item
                name="imageName"
                label=""
                rules={[
                  {
                    required: true,
                    message: ""
                  }
                ]}
                hasFeedback
              >
                <Input addonAfter={<Tooltip title="TAG"> <QuestionCircleOutlined /> </Tooltip>}
             />
              
                 
              </Form.Item>
             
             
            </Col>

            <Col span={11}>
              <Form.Item
                name="imagePos"
                label=""
                rules={[
                  {
                    required: true,
                    message: ""
                  }
                ]}
                hasFeedback
              >
                <Input addonAfter={<Tooltip title=" /"> <QuestionCircleOutlined /> </Tooltip>}/>
              </Form.Item>
            </Col>
            <Col span={11} offset={2}>
              {/* <span>?</span> */}
              <Form.Item
                name="imageDes"
                label=""
                rules={[
                  {
                    required: true,
                    message: ""
                  }
                ]}
                hasFeedback
              >
                <Input />
              </Form.Item>
            </Col>

            <Col span={24}>
              <Form.Item
                name="expSynopsis"
                label=""
                rules={[
                  {
                    required: true,
                    message: ""
                  }
                ]}
                hasFeedback
              >
                <TextArea autoSize />
              </Form.Item>
            </Col>
            <Col span={24}>
              <Form.Item
                name="expTask"
                label=""
                rules={[
                  {
                    required: true,
                    message: ""
                  }
                ]}
                hasFeedback
              >
                <TextArea autoSize />
              </Form.Item>
            </Col>
            <Col span={12}>
              <Form.Item>
                <ReactQuill
                  theme="snow"
                  value={this.state.value}
                  onChange={this.handleChange}
                  className=" ql-editor"
                />
              </Form.Item>
            </Col>
            <Col span={10} offset={2}>
              <div style={showHtmlStyle}>
                <div dangerouslySetInnerHTML={{ __html: this.state.value }} />
              </div>
            </Col>
          </Row>

          <Form.Item {...tailFormItemLayout}>
            <Button type="primary" htmlType="submit">
              
            </Button>
          </Form.Item>
        </Form>
        {/* {el.html(this.state.value)} */}
      </div>
    );
  }
}
