
import React, { Component } from "react";
import {  Form, Input, Modal, Button } from "antd";
import { PlusOutlined } from "@ant-design/icons";

const layout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 20 },
};

const validateMessages = {
  required: '${label} is required!',
  types: {
    number: '${label} is not a validate number!',
  },
  number: {
    range: '${label} must be between ${min} and ${max}',
  },
};

export default class AddManager extends Component {
  
  state = {
    ModalText: '',
    visible: false,
    confirmLoading: false,
  };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = () => {
    this.setState({
      ModalText: 'The modal will be closed after two seconds',
      confirmLoading: true,
    });
    setTimeout(() => {
      this.setState({
        visible: false,
        confirmLoading: false,
      });
    }, 2000);
  };

  handleCancel = () => {
    this.setState({
      visible: false,
    });
  };

  render() {
    const { visible, confirmLoading } = this.state;
    return (
      <>
        <Button type="primary" onClick={this.showModal} >
          <PlusOutlined />
          
        </Button>
        <Modal
          title=""
          visible={visible}
          onOk={this.handleOk}
          confirmLoading={confirmLoading}
          onCancel={this.handleCancel}
        >
                <Form {...layout} name="nest-messages" validateMessages={validateMessages}>
                <Form.Item name={['user', 'name']} label="" rules={[{ required: true,message:"" }]}hasFeedback>
                  <Input placeholder=''/>
                </Form.Item>
                <Form.Item name={['user', 'tel']} label="" rules={[{min:11,max:11,required: true,message:""}]} hasFeedback>
                  <Input placeholder='11' />
                </Form.Item>
                <Form.Item name={['user', 'password']} label="" rules={[{ type: 'password', min: 6, max: 30,required: true,message:"" }]}>
                  <Input.Password placeholder= '6'/>
                </Form.Item>                
              </Form>
        </Modal>
      </>
    );
  }
}

