import React, { Component } from "react";
import { Table, Input, message, Button, Row, Col, Modal } from "antd";

import { reqPeopleManage, reqPeopleDelete } from "../../api/index.js";
import store from "../../store/index.js";
import "./peopleManageCom.css";

let initState = {
  pageNum: "",
  pageSize: "",
  className: ""
};

//userId
let deleteUserId = [];
let myArr = [];

export default class PeopleManageCom extends Component {
  constructor(props) {
    super(props);

    this.state = {
      //
      pageSize: 10,
      //
      pageNum: 1,
      //
      records: [],
      //
      total: 1,
      //
      pages: 1,
      //
      isLoading: true,
      //
      position: ["bottomCenter"],
      //Id
      adminId: "1",
      //
      className: "45",
      //
      userName: "",
      //keykeyuserID
      selectedRowKeys: [],
      //
      loading: false,
      //Id
      selectLength: 0,
      //
      visible: false,
      isSelected: false,
      myArr: []
    };
  }

  /**
   * @description: 
   * @param {pageNum }
   * @return: 
   */
  handleTableChange = pageNum => {
    this.setState(
      () => {
        return { pageNum: pageNum };
      },
      () => {
        const { pageSize, adminId, className } = initState;
        const { pageNum } = this.state;
        this.funcReqPeopleManage(pageNum, pageSize, adminId, className);
      }
    );
  };

  /**
   * @description: 
   * @param {pageNum , pageSize , adminId ID, className 12, userName }
   * @return:
   */
  funcReqPeopleManage = (pageNum, pageSize, adminId, className, userName) => {
    reqPeopleManage(pageNum, pageSize, adminId, className, userName)
      .then(response => {
        var array = new Array();
        if (response.isSuccess) {
          /**
           * @description: recordkeycheckbox
           * @param {type}
           * @return:
           */
          for (var i = 0; i < response.data.records.length; i++) {
            var obj = new Object();
            obj.key = response.data.records[i].userId;
            obj.userStudentNumber = response.data.records[i].userStudentNumber;
            obj.userId = response.data.records[i].userId;
            obj.userName = response.data.records[i].userName;
            obj.userClass = response.data.records[i].userClass;
            obj.className = response.data.records[i].className;
            array[i] = obj;
          }

          for (var i = 0; i < this.state.selectedRowKeys.length; i++) {
            deleteUserId.push(this.state.selectedRowKeys[i]);
          }
          this.setState({
            records: array,
            //
            total: response.data.total,
            //
            pages: response.data.pages,
            //
            pageNum: response.data.current,
            //
            pageSize: response.data.size,
            isLoading: false
          });
        } else {
          message.error(response.message);
        }
      })
      .catch(error => {
        message.error(error.message);
      });
  };

  /**
   * @description: storestate
   * @param {type}
   * @return:
   */
  componentWillMount() {
    this.setState(
      () => {
        return {
          pageNum: store.getState().pageNum,
          pageSize: store.getState().pageSize,
          adminId: store.getState().adminId,
          className: store.getState().className,
          count: true
        };
      },
      () => {
        const { pageNum, pageSize, adminId, className } = this.state;
        initState.pageNum = pageNum;
        initState.pageSize = pageSize;
        initState.adminId = adminId;
        initState.className = className;
        this.funcReqPeopleManage(
          initState.pageNum,
          initState.pageSize,
          initState.adminId,
          initState.className
        );
      }
    );
    deleteUserId = [];
    myArr = [];
  }

  /**
   * @description: 
   * @param {value }
   * @return:
   */
  searchFunc = value => {
    const { pageNum, pageSize, className, adminId } = this.state;
    reqPeopleManage(pageNum, pageSize, adminId, className, value)
      .then(response => {
        if (response.isSuccess) {
          for (var i = 0; i < this.state.selectedRowKeys.length; i++) {
            let a = this.state.selectedRowKeys[i];
            deleteUserId.push(a);
          }
          var array = new Array();
          for (var i = 0; i < response.data.records.length; i++) {
            var obj = new Object();
            obj.key = response.data.records[i].userId;
            obj.userStudentNumber = response.data.records[i].userStudentNumber;
            obj.userId = response.data.records[i].userId;
            obj.userName = response.data.records[i].userName;
            obj.userClass = response.data.records[i].userClass;
            obj.className = response.data.records[i].className;
            array[i] = obj;
          }
          this.setState({
            records: array,
            //
            total: response.data.total,
            //
            pages: response.data.pages,
            //
            pageNum: response.data.current,
            //
            pageSize: response.data.size,
            isLoading: false,
            isSelected: true
          });
        } else {
          message.error(response.message);
        }
      })
      .catch(error => {
      });
  };

  /**
   * @description: 
   * @param {arr }
   * @return: 
   */
  unique = arr => {
    var res = [];
    var obj = {};
    for (var i = 0; i < arr.length; i++) {
      if (!obj[arr[i]]) {
        obj[arr[i]] = 1;
        res.push(arr[i]);
      }
    }
    return res;
  };

  /**
   * @description: selectedRowKeysdeleteUserIdmyArr
   * @param {type}
   * @return:
   */
  start = () => {
    for (var i = 0; i < this.state.selectedRowKeys.length; i++) {
      deleteUserId.push(this.state.selectedRowKeys[i]);
    }
    var myArr = this.unique(deleteUserId);
    this.setState(
      () => {
        return { myArr: myArr };
      },
      () => {
        this.setState({
          visible: true
        });
      }
    );
  };

  /**
   * @description: 
   * @param {e }
   * @return:
   */
  handleOk = e => {
    this.setState({
      visible: false,
      isSelected: false
    });
    reqPeopleDelete(this.state.myArr)
      .then(response => {
        if (response.isSuccess) {
          message.success(response.message);
          this.funcReqPeopleManage(
            initState.pageNum,
            initState.pageSize,
            initState.adminId,
            initState.className
          );
        } else {
          message.error(response.message);
        }
      })
      .catch(error => {
        message.error(error.message);
      });
  };

 /**
   * @description: 
   * @param {e }
   * @return:
   */
  handleCancel = e => {
    this.setState({
      visible: false,
      selectedRowKeys: [],
      isSelected: false
    });
    //
    deleteUserId = [];
    myArr = [];
  };

  /**
   * @description: 
   * @param {selectedRowKeys}
   * @return:
   */
  onSelectChange = (selectedRowKeys, selectedRows) => {
    this.setState({ selectedRowKeys });
  };
  
  render() {
    const {
      records,
      pageNum,
      total,
      pageSize,
      position,
      className
    } = this.state;

    const columns = [
      {
        title: "",
        dataIndex: "userId",
        key: "userId",
        //
        sorter: (a, b) => a.key - b.key,
        //
        render: (text, record, index) =>
          `${pageSize * (pageNum - 1) + index + 1}`,
        //
        width: "5vw",
        fixed: "left"
      },
      {
        title: "",
        dataIndex: "userStudentNumber",
        key: "userStudentNumber",
        sorter: (a, b) => a.key - b.key,
        width: "10vw"
      },
      {
        title: "",
        dataIndex: "userName",
        key: "userName",
        width: "7vw"
      },
      {
        title: "",
        dataIndex: "userClass",
        key: "userClass",
        width: "10vw"
      },
      {
        title: "",
        dataIndex: "className",
        key: "className",
        width: "10vw"
      }
    ];
    //
    const paginationProps = {
      //
      page: pageNum,
      //
      defaultPageSize: pageSize,
      //
      onChange: page => this.handleTableChange(page),
      //
      total: total,
      //
      position: position
    };

    const { Search } = Input;
    const { loading, selectedRowKeys } = this.state;

    //
    const rowSelection = {
      //selectedRowKeys key  onChange 
      selectedRowKeys,
      //
      onChange: this.onSelectChange
      // }
    };

    //
    const hasSelected = selectedRowKeys.length > 0;

    return (
      <div>
        <Row id="peopleBtn">
          <Col span={8}>
            <Button
              type="primary"
              onClick={this.start}
              disabled={!hasSelected}
              loading={loading}
              id="deleteBtnMy"
            >
              
            </Button>
            <Modal
              title=""
              visible={this.state.visible}
              onOk={this.handleOk}
              onCancel={this.handleCancel}
               okText=""
              cancelText=""
            >
              <p>{this.state.myArr.length}</p>
            </Modal>
          </Col>
          <Col span={12}>
            <Search
              placeholder=""
              enterButton=""
              size="large"
              onSearch={this.searchFunc}
            />
          </Col>{" "}
        </Row>

        <div>
          <Table
            columns={columns}
            bordered={true}
            dataSource={records}
            pagination={paginationProps}
            loading={this.state.isLoading}
            scroll={{ x: 1000 }}
            rowKey={record => record.key}
            rowSelection={rowSelection}
            id="peopleTable"
          />
        </div>
      </div>
    );
  }
}
