import React from "react";
import "./cHome.css";
import { Row, Col, message } from "antd";
import { Layout } from "antd";
import ConTable from "../../tools/homeTable";
import ConUpload from "../../tools/upload";

import echarts from "echarts/lib/echarts";
import "echarts/lib/chart/line"; //line,pie,bar
import "echarts/lib/component/tooltip";
import "echarts/lib/component/title";
import "echarts/lib/component/legend";
import "echarts/lib/component/markPoint";
import ReactEcharts from "echarts-for-react";
import { reqCPUCharts, reqStuCharts } from "../../../api/index.js";

const { Content, Footer } = Layout; //

export default class ConHome extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      //Id
      adminId: "1",
      timer: "",
      onlineStu: 0,
      expStudent: 0,
      cpuOccupied:0,
      totalCPU:0
    };
  }

  componentWillMount() {
    //willmounted
    echarts.registerTheme("Imooc");
    reqCPUCharts()
    .then(response => {
          if (response.isSuccess) {
            this.setState({
              //
              cpuOccupied:response.data.totalRAM - response.data.availableRAM,
              totalCPU:response.data.totalRAM
            });
          } else {
            message.error(response.message);
          }
        })
    .catch(error => {
          message.error(error.message);
        });

      //
      reqStuCharts()
        .then(response => {
          if (response.isSuccess) {
            this.setState({
              //
              onlineStu: response.data.usersNumVO.usersNum,
              expStudent: response.data.dockerNumVO.dockerNum
            });
          } else {
            message.error(response.message);
          }
        })
        .catch(error => {
          message.error(error.message);
        });
    // this.reqSetIntvalFunc();
  }

  /**
   * @description:  render()  React unmount 
   * @param {type}
   * @return {type}
   */
  componentWillUnmount() {
    if (this.timer != null) {
      clearInterval(this.timer);
    }
  }

  /**
   * @description: CPU
   * @param {type}
   * @return {type}
   */
  CPUOccupy = () => {
    let {cpuOccupied,totalCPU} = this.state;
    let CPUOccupyCharts = {
      title: {
        text: "",
        left: "center"
      },
      tooltip: {
        trigger: "item",
        formatter: "{a} <br/>{b}: {c} ({d}%)"
      },
      legend: {
        orient: "vertical",
        left: 10,
        data: ["", ""]
      },
      series: [
        {
          name: "",
          type: "pie",
          radius: ["50%", "70%"],
          avoidLabelOverlap: false,
          label: {
            show: false,
            position: "center"
          },
          emphasis: {
            label: {
              show: true,
              fontSize: "30",
              fontWeight: "bold"
            }
          },
          labelLine: {
            show: false
          },
          data: [
            { value: cpuOccupied, name: "" },
            { value: totalCPU-cpuOccupied, name: "" }
          ]
        }
      ]
    };
    return CPUOccupyCharts;
  };

  /**
   * @description: 
   * @param {type}
   * @return {type}
   */
  studentCharts = () => {
    let { onlineStu, expStudent } = this.state;
    var studentCharts = {
      tooltip: {
        trigger: "axis",
        axisPointer: {
          // 
          type: "shadow" // 'line' | 'shadow'
        }
      },
      legend: {
        data: ["", ""]
      },
      grid: {
        left: "3%",
        right: "4%",
        bottom: "3%",
        containLabel: true
      },
      xAxis: {
        type: "value"
      },
      yAxis: {
        type: "category",
        data: [""]
      },
      series: [
        {
          name: "",
          type: "bar",
          stack: "",
          label: {
            show: true,
            position: "insideRight"
          },
          data: [expStudent]
        },
        {
          name: "",
          type: "bar",
          stack: "",
          label: {
            show: true,
            position: "insideRight"
          },
          data: [onlineStu]
        }
      ]
    };
    return studentCharts;
  };

  /**
   * @description: 10s
   * @param {type}
   * @return {type}
   */

  reqSetIntvalFunc = () => {
    this.timer = setInterval(() => {
      //
      //CPU
      reqCPUCharts()
        .then(response => {
          if (response.isSuccess) {
            this.setState({
              //
              cpuOccupied:response.data.totalRAM - response.data.availableRAM,
              totalCPU:response.data.totalRAM
            });
          } else {
            message.error(response.message);
          }
        })
        .catch(error => {
          message.error(error.message);
        });

      //
      reqStuCharts()
        .then(response => {
          if (response.isSuccess) {
            this.setState({
              //
              onlineStu: response.data.usersNumVO.usersNum,
              expStudent: response.data.dockerNumVO.dockerNum
            });
          } else {
            message.error(response.message);
          }
        })
        .catch(error => {
          message.error(error.message);
        });
    }, 10000);
  };

  render() {
    return (
      <div className="cHomeCon">
        <Layout style={{ padding: "0 24px 24px", margin: "23px 0" }}>
          <Content className="site-layout-background BaseContent">
            {/* <div className="intro"> */}
            <Row className="firstPageRow">
              {/* */}
              <Col span={12}>
                <ReactEcharts option={this.CPUOccupy()} /> {/* </div> */}
              </Col>
              <Col span={12}>
                <ReactEcharts option={this.studentCharts()} />{" "}
              </Col>
            </Row>
            <Row className="firstPageRow">
              <Col span={4} className="firstPageCol">
                <h2></h2>
              </Col>
              <Col span={4} id="firstPageUpload">
                <ConUpload adminId={this.state.adminId} />
              </Col>
              <ConTable />
            </Row>
          </Content>
          {/* <Footer style={{ textAlign: "center", height: "5px" }}>
            2020 
          </Footer> */}
        </Layout>
      </div>
    );
  }
}
