import React from "react";
import { Table, Tooltip, message, Col, Modal, Row, Upload, Button} from 'antd';
import "./table.css"
import { DeleteOutlined,UploadOutlined } from '@ant-design/icons';
import { reqHomeTabShow, reqHomeTabDelete } from "../../api/index.js";
import ConUpload from "./upload";

export default class ConTable extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      //
      records: [],
      //
      total: 1,
      //Id
      adminId: "1",
      //
      isLoading: false,
      records: "",
      materialId: "",
      materialName:"",
      //modal
      visible: false,
      //modal
      deleteVisible: false,
      // 
      isDelete: false
    };
  }

  /**
     * @description:
     * @param {materialId ID}
     * @return:
     */
  funcReqMaterial = materialId => {
    reqHomeTabShow()
      .then(response => {
        if (response.isSuccess) {
          this.setState({
            records: response.data,
            isLoading: false
          });
        } else {
          message.error(response.message);
        }
      })
      .catch(error => {
        message.error(error.message);
      });
  };

  /**
     * @description:Id
     * @param {type}
     * @return:
     */
  componentWillMount() {
    this.setState(
      //no sure
      () => {
        return {
          adminId: this.state.adminId,
          materialId: this.state.materialId
        };
      },
      () => {
        const { materialId } = this.state;
        this.funcReqMaterial(this.state.materialId);
      }
    );
  }

/**
 * @description: 
 * @param {id ID}
 * @return:
 */
  handleDeleteMaterial = (fileName,id) => {
    reqHomeTabDelete(fileName, id)
      .then(response => {
        if (response.isSuccess) {
          this.setState(
            () => {
              return { isDelete: true };
            },
            () => {
              this.funcReqMaterial(this.state.id);
              this.setState({ isDelete: false });
            }
          );
          message.success("");
        } else {
          message.error("");
        }
      })
      .catch(error => {
        message.error(error.message);
      });
  };

/**
 * @description: 
 * @param {type}
 * @return:
 */
  showDeleteModal = text => {
    this.setState({
      deleteVisible: true,
      materialId: text.materialId,
      materialName: text.materialName
    });
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   */
  handleOk = e => {
    
    this.setState({
      visible: false
    });
  };

  handleCancel = e => {
    
    this.setState({
      visible: false
    });
  };

  deleteHandleOk = e => {
    
    this.setState({
      deleteVisible: false
    });
    this.handleDeleteMaterial(this.state.materialName,this.state.materialId);
  };

  deleteHandleCancel = e => {
    
    this.setState({
      deleteVisible: false
    });
  };

  render() {
    const columns = [
      {
        title: "",
        dataIndex: "key",
        key: "key",
        fixed: 'left',
        //
        sorter: (a, b) => a.key - b.key,
        //
        render: (text, record, index) => `${index + 1}`,
        //
        width: "8%"
      },
      {
        title: '',
        dataIndex: 'materialName',
        key: 'materialName',
        width: "32%",
        ellipsis: {
          showTitle: false
        },
        render: materialName => (
          <Tooltip placement="topLeft" title={materialName}>
            {materialName}
          </Tooltip>
        )
      },
      {
        title: '',
        dataIndex: 'materialUrl',
        key: 'materialUrl',
        width: "34%",
        ellipsis: {
          showTitle: false
        },
        render: materialUrl => (
          <Tooltip placement="topLeft" title={materialUrl}>
            {materialUrl}
          </Tooltip>
        )
      },
      {
        title: '',
        dataIndex: 'adminName',
        key: 'adminName',
        width: "10%",
      },
      {
        title: '',
        dataIndex: 'materialId',
        key: 'materialId',
        width: "9%",
      },
      {
        title: '',
        dataIndex: 'action',
        key: 'action',
        fixed: 'right',
        width: "5%",
        render: (text, record, index) => {
          return (
            <div>
              <Button onClick={() => this.showDeleteModal(record)} shape="circle">
                <DeleteOutlined />
              </Button>
              <Modal
                title=""
                visible={this.state.deleteVisible}
                onOk={this.deleteHandleOk}
                onCancel={this.deleteHandleCancel}
                okText=""
                cancelText=""
              >
                <p>?</p>
              </Modal>
            </div>
          );
        }
      }
    ];
    return (
        <Table
          columns={columns}
          dataSource={this.state.records}
          scroll={{ x: 1200,y:250 }}
          //
          pagination={false}
          className="table"
        />
    );
  }
}