import store from 'store'

const USER_KEY = 'user'

export default{
    //userStudentNumber
    saveUser(user){
        store.set(USER_KEY,user)
    },
    //userStudentNumber
    getUser(){
        // 
        return store.get(USER_KEY) || {}
    },
    //userStudentNumber
    removeUser(){
        store.remove(USER_KEY)
    },

};